
import Divider from 'vux/src/components/divider/index.vue'
import Panel from 'vux/src/components/panel/index.vue'


export default {
components: {
Divider,
Panel
},
data () {
return {
lists: []
}
},
methods: {
getWarn () {
alert(1)
}
},
mounted () {
var _this = this;
this.$http({
methods: 'post',
url: '/static/data/top50.json'
})
.then( (res) => {
res.data.subjects.forEach( (item, i) => {
let obj = {};

obj.type = item.title;

obj.list = [];
let list1 = {};
list1.src = item.images.medium;
list1.fallbackSrc = item.images.medium;
list1.title = item.directors[0].name;
list1.desc = item.title;
list1.url = "#";
obj.list.push(list1);

let meta = {};
meta.source = item.directors[0].name;
obj.list.push(meta);

obj.footer = {};
obj.footer.title = "查看更多";
obj.footer.url = "#";

_this.lists.push(obj);
})
})
.catch( (err) => {
alert(err)
})
}
}



